<template>
  <div class="table">
    <h2>全国空气质量指数实时排行榜&nbsp;最新获取时间{{update_time}}</h2>
    <el-table :data="ranktable.slice((currentPage - 1) * pagesize, currentPage * pagesize)" lazy     
    :header-cell-style="{'text-align':'center'}"
    :cell-style="{'text-align':'center'}"
    style="width: 100%">>
      <el-table-column type="index" label="排名" width='200px'/>
      <el-table-column prop="city" label="城市" />
      <el-table-column prop="aqi" label="空气质量指数" />
      <el-table-column prop="aqi_level" label="空气质量等级" />
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[50, ranktable.length]"
      :page-size="pagesize"
      :total="ranktable.length"
      layout="total, sizes, prev, pager, next, jumper"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'rank',
  data() {
    return {
      currentPage: 1,
      pagesize: 50,
      ranktable: '',
      update_time: '',
    }
  },
  created() {
    let _this = this
    const axios = require('axios');
    axios.get('https://v0.yiketianqi.com/aqi/rankcity?appid=39284283&appsecret=QOJkF6pN')
      .then(res => {
        _this.ranktable = res.data.list;
        _this.update_time = res.data.update_time
        _this.ranktable.reverse()
      })
      .catch(err => {
        console.error(err);
      })
  },
  methods: {
    handleSizeChange: function (val) {
      this.pagesize = val;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },
  },
}
</script>
<style>
.table {
width: auto;
height: auto;
margin-left: 100px;
margin-right: 100px;
padding-bottom: 15px;
}
h2 {
  float: left;
}
</style>